export const convertNumber = (number) => {
  var newNumber = number.toString().replace(/(\d)(?=(\d{3})+(?!\d))/g, "$1,");
  return newNumber;
  // parser={(value) => value.replace(/\$\s?|(,*)/g, "")}
};

export const onKeyDown = (e) => {
  if (e.keyCode === 13) {
    e.preventDefault();
  }
};